@media (min-width: 350px) and (max-width: 480px) {

  body {
    padding: 10px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 16px;
  }

  .shop {
    border: 1px solid rgb(255, 255, 255);
    margin: 10px;
    padding: 10px;
  }

  h1 {
    font-size: 20px;
    text-align: center;
    background: linear-gradient(rgb(221, 52, 52), rgb(125, 29, 29), rgb(0, 0, 0));
  }

  .card {
    padding: 10px;
    border: 1px solid rgb(240, 134, 14);
    border-radius: 5px;
    margin-bottom: 20px;
  }

  .title {
    color: rgb(244, 76, 104);
    font-weight: 600;
    font-size: 18px;
  }

  .vitrine {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }

  button {
    background-color: rgb(56, 111, 14);
    width: 27%;
    padding: 3px;
    color: white;
    margin-left: 10px;
  }

  .font {
    display: flex;
    text-align: center;
  }
}

